
.Toolbar {
	height: 5.6rem;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: var(--color-primary);
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 2rem;
	box-sizing: border-box;
	z-index: 90;
}

.Toolbar nav {
	height: 100%;
}

.Logo {
	height: 80%;
}

@media (max-width: 499px) {
	.DesktopOnly {
			display: none;
	}
}